import back from '@/components/back'
import noData from '@/components/noData'
import { log } from 'util'

export default {
    components:{
        back,
        noData
    },
    data(){
        return{
            search: this.$store.state.searchText,
            id: this.$route.query.id,
            list: [],
            Um: [],
            from: this.$route.query.from,
            page: 1,
            size: 15,
            haveMore: true,
            user: this.$store.state.userInfo,
            ListDetails: this.$store.state.upList,
            showConfirm: false,
            pageShow: false,
            medSearchList:null,
			medSearchList2:null,
            inputFlag: false,
			scrollTopDistance:0,
			listShow:true,
			defaultImg: 'this.src="' + require('../../assets/images/prescriptionList.png') + '"',
			demo06_list: [],
			demo06_index: 0,
			imgArr:[],
			info:"",
			text:'',
			showH:false,
			price:'',
			Function:'',
			defaultImg2: 'this.src="' + require('../../assets/images/prescription-img.png') + '"',
			contentSH:false,
			medBlurSH:false,
			star: this.$route.query.star,
			IsClassify:false,
			items:[],
			itemsNum:0,
			children:[],
			itemsChildNum:0,
			children2:[],
			saveChooseMed:this.$store.state.saveChooseMed,
			saveChooseMedText:null,
			ChildMedText:null,
			SortType:this.$store.state.SortType?this.$store.state.SortType.SortType:0,//排序类型（0：默认排序，1：价格排序，2：活跃度排序）
			SortTypeDirection:this.$store.state.SortType?this.$store.state.SortType.SortTypeDirection:0, //排序方向（0：默认降序，1：升序）
        }
    },
    computed:{
        totalPrice(){
            let p = 0
            if(this.ListDetails.length){
                this.ListDetails.forEach(v => {
                    p += (+v.Price) * v.Quantity
                })
            }
            return p.toFixed(2)
        }
    },
    created(){
		//this.pull(false)
		//this.pullNavList()
		//console.log(this.$store.state.saveChooseMed)
		this.search = ''
    },
    mounted(){
		this.getMore()
		this.reload()
    },
    beforeDestroy(){
        window.onscroll = null
	},
	beforeRouteLeave(to, from, next) {
		if(from.meta.keepAlive) {　
			from.meta.scollTopPosition = this.scrollTopDistance;
		}
		next()
	},
	beforeRouteEnter(to, from, next) { //进入前
		next(vm => {
			vm.getMore()
			vm.IsClassify = false
			vm.saveChooseMed = vm.$store.state.saveChooseMed
			vm.SortType = vm.$store.state.SortType?vm.$store.state.SortType.SortType:0
			vm.SortTypeDirection=vm.$store.state.SortType?vm.$store.state.SortType.SortTypeDirection:0
			let flag = false
			vm.search = ''
			if((from.path != vm.$store.state.prevUrl && vm.$store.state.prevUrl != null) || !vm.$store.state.prevUrl || vm.$store.state.isFresh) {
				flag = true
			}
			if(flag) {
				vm.allTomedChild(vm.$store.state.saveChildMed)
				vm.pullNavList()
				if(vm.$store.state.pullFlag){
					vm.$refs.el2.scrollTop = to.meta.scollTopPosition
				}
			} else {
				vm.$refs.el2.scrollTop = to.meta.scollTopPosition
			}
		});
	},
    methods:{
		ChooseMedList(){
			if(this.IsClassify === true){
				this.IsClassify = false
			}else{
				this.IsClassify = true
			}
		},
		medBlur(){
			let that = this;
			setTimeout(function(){
				that.medBlurSH = false
			},100)
		},
		medInputMethods(){
			if(localStorage.getItem('searchName')){
				this.medSearchList2 = JSON.parse(localStorage.getItem('searchName')).slice(0,10)
				this.medBlurSH = true
			}
		},
        searchFor(search){
// 			if(!this.search){
// 				return
// 			}	
			if(!localStorage.getItem('searchName')){
				var searchArr = []
			}else{
				var searchArr = JSON.parse(localStorage.getItem('searchName'))
			}
			if(searchArr.indexOf(this.search) == -1 && this.search){
				searchArr.unshift(this.search)
			}
			localStorage.setItem('searchName', JSON.stringify(searchArr))
            this.$store.dispatch('pushsearchText',this.search)
            if(this.$route.query.from == 'p'){
		        this.$router.push(`/medicine-list?id=&from=ump`)
		    }
		    else if(this.$route.query.from == 'up'){
		        this.$router.push(`/medicine-list?id=&from=upp`)
		    }
		    else{
		        this.$router.push(`/medicine-list?id=&from=um`)
		    }
        },
        BackTo(){
			if(this.$store.state.upListNum.num && (this.$route.query.from === 'p' || this.$route.query.from === 'ump')){
				this.$router.push(`/prescription`)
			}
            else if(this.$route.query.from === 'up' || this.$route.query.from === 'upp'){
                this.$router.push(`/usup-detail`)
            }
            else{
                this.$router.push(`/prescription`)
            }
		},
		pullNavList(){ //一及分类
			this.$post('Prescription/Medication/GetMedicationClass',{
				ParentId:0,
                DoctorId:this.user.Data.User.DoctorId,
                OrgId:this.user.Data.User.OrgId,
			}).then(o => {
				let arr ={
					Name: "明星药"
				}
				o.unshift(arr)
				this.items = o
				if(this.saveChooseMed === 'star'){
					this.itemsNum = 0
					this.medChild(this.saveChooseMed,0)
				}else{
					this.items.forEach((v,k) => {
						if(v.Id === this.saveChooseMed.Id){
							this.itemsNum = k
							this.medChild(this.saveChooseMed,k)
						}
					})
				}
				
			})
		},
		medChild(i,idx){  //二及分类
			this.itemsNum = idx
			let ParentId = 0
			if(idx < 1 || i === 'star'){
				this.children = [{
					Name: "全部"
				}]
				this.saveChooseMedText = 'star'
			}else{
				this.$post('Prescription/Medication/GetMedicationClass',{
					ParentId:i.Id,
					DoctorId:this.user.Data.User.DoctorId,
					OrgId:this.user.Data.User.OrgId,
				}).then(o => {
					this.children = o
				})
				this.saveChooseMedText = i
			}
		},
		allTomedChild(i){ //默认二及分类
			var MedicationClassId = 0
			if(this.saveChooseMed === 'star'){
				this.children2 = [{
					Name: "全部"
				}]
				this.ChildMedText = this.children2
				this.medSearchList = null
				this.page = 1
				this.haveMore = true
				this.pull(false,MedicationClassId)
			}else{
				this.$post('Prescription/Medication/GetMedicationClass',{
					ParentId:this.saveChooseMed.Id,
					DoctorId:this.user.Data.User.DoctorId,
					OrgId:this.user.Data.User.OrgId,
				}).then(o => {
					this.children2 = o
					if(i){
						this.ChildMedText = i
						
					}else{
						this.ChildMedText = o[0]
					}
					MedicationClassId = this.ChildMedText.Id
					this.medSearchList = null
					this.page = 1
					this.haveMore = true     
					this.pull(false,MedicationClassId)
				})
			}
			
		},
		medChildList(i,idy){
			this.saveChooseMed = this.saveChooseMedText
			this.allTomedChild(i)
			this.IsClassify = false
			this.ChildMedText = i
			this.medSearchList = null
            this.page = 1
			this.haveMore = true
			this.SortType = 0  
			this.SortTypeDirection = 0
			this.pull(false,this.ChildMedText.Id)
			this.$store.dispatch('pushsaveChooseMed', this.saveChooseMedText)
			this.$store.dispatch('pushsaveChildMed', i)
			this.$store.dispatch('pushSortType', null)
		},
		medChildList2(i,idy){
			this.ChildMedText = i
			this.medSearchList = null
            this.page = 1
			this.haveMore = true
			this.SortType = 0  
			this.SortTypeDirection = 0
			this.pull(false,this.ChildMedText.Id)
			this.$store.dispatch('pushsaveChildMed', i)
			this.$store.dispatch('pushSortType', null)
		},
		moneyClick(){
			this.SortType = 1
			if(this.SortTypeDirection){
				this.SortTypeDirection = 0
			}else{
				this.SortTypeDirection = 1
			}
			this.medSearchList = null
            this.page = 1
			this.haveMore = true
			if(this.ChildMedText && this.ChildMedText[0]){
				this.pull(false,0)
			}else{
				this.pull(false,this.ChildMedText.Id)
			}
			let arr ={
				SortType:this.SortType,
				SortTypeDirection:this.SortTypeDirection,
			}
			this.$store.dispatch('pushSortType', arr)
			
		},
		briskClick(){
			this.SortType = 2
			if(this.SortTypeDirection){
				this.SortTypeDirection = 0
			}else{
				this.SortTypeDirection = 1
			}
			this.medSearchList = null
            this.page = 1
			this.haveMore = true
			if(this.ChildMedText && this.ChildMedText[0]){
				this.pull(false,0)
			}else{
				this.pull(false,this.ChildMedText.Id)
			}
			let arr ={
				SortType:this.SortType,
				SortTypeDirection:this.SortTypeDirection,
			}
			this.$store.dispatch('pushSortType', arr)

		},
        pull(h,MedicationClassId){
            if(this.haveMore){
				let url = ''
				if(this.ChildMedText && this.ChildMedText[0]){
					url = 'Prescription/Medication/GetMedicationByNameForStar'
				}else{
					url = 'Prescription/Medication/GetMedicationByNameForOftenUsed'
				}
                this.$post(url,this.$pick({
                    MedicationName: this.search,
                    OrgId:this.user.Data.User.OrgId,
                    DoctorId:this.user.Data.User.DoctorId,
					MedicationClassId,
					SortType:this.SortType,          //排序类型（0：默认排序，1：价格排序，2：活跃度排序）
					SortTypeDirection:this.SortTypeDirection, //排序方向（0：默认降序，1：升序）
                    PageIndex: this.page,
                    PageSize: this.size
                })).then(o => {
					this.listShow = true
                    this.pageShow = true
                    if (!o.ReturnData || o.ReturnData.length < 15){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }
                    if (h){
                        o.ReturnData.forEach(v => {
                            this.list.push(v)
                        })
                    }
                    else {
                        this.list = o.ReturnData
                    }
					if(o.ReturnData ){
						this.list.forEach(a => {
							a.state = ''
						})
					}
					if(this.from === 'up' || this.from === 'upp'){
					    const list = this.$store.state.upList
						
					    list.forEach(a => {
					        this.list.forEach(v => {
					            if(v.MedicationId === a.MedicationId){
					                v.state = 'onUp'
					            }
					        })
					    })
					}
					else if(this.from === 'p' || this.from === 'ump'){
					    const list = this.$store.state.upList
					    list.forEach(a => {
					        this.list.forEach(v => {
					            if(v.MedicationId === a.MedicationId){
					                v.state = 'onP'
					            }
					        })
					    })
					}
                })
            }
        },
		addList(){
			if(this.from === 'up' || this.from === 'upp'){
			    const list = this.$store.state.upList
			    list.forEach(a => {
			        this.list.forEach(v => {
			            if(v.MedicationId === a.MedicationId){
			                v.state = 'onUp'
			            }
			        })
			    })
			}
			else if(this.from === 'p' || this.from === 'ump'){
			    const list = this.$store.state.upList
			    list.forEach(a => {
			        this.list.forEach(v => {
			            if(v.MedicationId === a.MedicationId){
			                v.state = 'onP'
			            }
			        })
			    })
			}
		},
		pull2(id){
			this.$router.push('/prescription-detail/'+id)
		},
        toUm(i,idx){
            this.$post('Prescription/Medication/SaveMedicationOftenUsed',{
                MedicationId: i.Id,
                MedicationCompanyId: i.MedicationCompanyId,
                OrgId: this.user.Data.User.OrgId,
                DoctorId: this.user.Data.User.DoctorId
            }).then(o => {
                this.list[idx].IsAdd = true
            })
        },
        toUp(i){
            if(this.$route.query.from == 'ump' || this.$route.query.from == 'upp'){
                this.$store.dispatch('pushcMed', i)
                this.$router.push('/consumption?back=-3')
            }
            else{
                this.$store.dispatch('pushcMed', i)
                this.$router.push('/consumption?back=-4')
            }
        },
        getMore(){
            const el2 = this.$refs.el2
            let timer = null
            el2.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
					 const top = el2.scrollTop
								|| el2.pageYOffset
								|| el2.scrollTop,
                            h = el2.scrollHeight,
                            wh = window.innerHeight
					this.scrollTopDistance = top;
					console.log(this.scrollTopDistance)
                    if (h - top - wh < 120 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        },
        confirm(){
            this.ListDetails.forEach((v,k) => {
                if(v.Id == this.removeI.Id){
                    this.ListDetails.splice(k,1)
                }
            })
            this.list[this.removeIdx].state = null
        },
        delToPre(i,idx){
            this.removeI = i
            this.removeIdx = idx
            this.showConfirm = true
        },
        toList(i){
			if(!localStorage.getItem('searchName')){
				var searchArr = []
			}else{
				var searchArr = JSON.parse(localStorage.getItem('searchName'))
			}
			if(searchArr.indexOf(i) == -1){
				searchArr.unshift(i)
			}
			localStorage.setItem('searchName', JSON.stringify(searchArr))
            this.$store.dispatch('pushsearchText',i)
            this.$router.push(`/medicine-list?from=${this.$route.query.from}`)
        },
        searchByName(){
            if(this.inputFlag){
                return
            }
			let url = ''
			if(this.star){
				url = 'Prescription/Medication/GetMedicationByNameForStar'
			}else{
				url = 'Prescription/Medication/GetMedicationByNameForOftenUsed'
			}
            if(this.search){
				this.$post(url,this.$pick({
					MedicationName: this.search,
                    OrgId: this.user.Data.User.OrgId,
					DoctorId:this.user.Data.User.DoctorId,
					MedicationClassId:this.id,
					PageIndex: this.page,
					PageSize: 20
				})).then(o => {
					this.medSearchList = o.ReturnData
				})
			}
        },
		reload(){
			//ios页面卡死问题
			let el2 = this.$refs.el2
			let lastY = 0;
			el2.addEventListener('touchstart', function(event) {
				lastY = event.touches[0].clientY;
			});
			//获取滚动元素
			let scrollEle =this.$refs.el2
			//先移除监听事件
			scrollEle.removeEventListener('touchmove', function (event) {
				event.preventDefault();
			}, false);
			//再添加监听事件
			scrollEle.addEventListener('touchmove', function(event) {
				let top = event.touches[0].clientY;
				let scrollTop = scrollEle.scrollTop;
				let scrollHeight = scrollEle.scrollHeight;
				let clientHeight = scrollEle.clientHeight;
				let direction = (lastY - top) < 0 ? "up" : "down";
				if (scrollTop == 0 && direction == "up") {
				  event.preventDefault();
				} else if (scrollTop >= (scrollHeight - clientHeight) && direction == "down") {
				  event.preventDefault();
				}
				lastY = top;
			});
		  
		}
    }
}



